const DataMap: Map<number, IRealmCardData> = DATA.reduce(
  (m: Map<number, IRealmCardData>, i: any) => {
    m.set(i.id, i);
    return m;
  },
  new Map()
);

export const getRealmCardData = (itemId: number) => DataMap.get(itemId);
