SCORE_DATA.sort((a, b) => b.score - a.score);

export function getEquipScoreData(equip: IHeroEquip): IScoreData | null {
  for (let item of SCORE_DATA) {
    if (match(equip, item)) {
      return item;
    }
  }
  return null;
}
